  text-align: center;
  font-size: 12px;
  height: 36px;
  line-height: 32px;
  border-radius: 0;
  background: none;
  border: 2px solid #222222;
  color: #222222;
}
.woocommerce a.button:hover, .woocommerce button.button:hover, .woocommerce input.button:hover,
.woocommerce-page a.button:hover,
.woocommerce-page button.button:hover,
