.topnav-label span{
	color: #707070;
}
.header-block{
	margin-bottom: -30px;
	padding: 0;
}

.topnav-label{
font-family: "Montserrat",sans-serif;;
}

.site-navigation .navbar-header a.navbar-brand .site-tagline{
	text-align: left;
	margin: 0px;
	line-height: normal;
	font-size:23px;
	font-family: 'Lora', serif; /* from google fonts */
	color:#000000;
}


.site-with-text{
	line-height: 25px;
	font-size:45px;
}

.navbar-header{
	border: solid 0px #000;
}

.search-box{
text-align: right;
border: solid 0px #000;
width: auto;
float: right;
}

.search-box .contact{
	font-weight: normal;
	font-size: 16px;
}

.search-box .search{
	border: solid 0px #000;
	padding: 5px 0px 5px 0px;
	
}

.search-box .search input[type="text"]{
border: solid 1px #000000;
padding: 5px 4px 5px 4px;
width: auto;
font-size: 13px;
}

.search-box .search input[type="submit"]{
border: solid 1px #000000;
padding: 5px 10px 5px 10px;
font-size: 13px;
background-color: #000000;
color: #ffffff;
}

.shipping{
color: #000;
font-size: 13px;
text-align: left;
padding-left: 5px;
}

.shipping .free{
color: #ff0000;
}

.navbar-brand{
	
}
.navbar-brand .site-tagline{
border: solid 0px #000;
font-size: 50px;
}

#menu-nar-header-menu{
	border: solid 0px #000;
	margin: 20px 0 0 0;
	padding: 0;
	width: 100%;
}

#menu-nar-header-menu li{
	
}
#menu-nar-header-menu li a{
border: solid 0px #000;
padding: 7px 13px 7px 13px;
margin: 0;
font-size: 16px;
font-family: 'Oswald', sans-serif;
}

#menu-nar-header-menu li ul{
	margin: 0;
	padding: 0;
	border:none;
	border-bottom: solid 1px #ccc;
	
}
#menu-nar-header-menu li ul li{
	border: solid 1px #ccc;
	border-bottom: none;
}
#menu-nar-header-menu li ul li a{
font-weight: normal;
text-transform: capitalize;
}



