/*--------------------------------------------------------------
>>> TABLE OF CONTENTS:
----------------------------------------------------------------
1.0 Global Style
2.0 Header
3.0 Content
4.0 Post Format
5.0 Comment
6.0 Footrt
7.0 Widgets
8.0 Page Templates
9.0 Contact Page Template
--------------------------------------------------------------*/
/* 1.0 GLOBAL STYLE
-------------------------------------------------------------- */
@media (min-width: 1200px) {
  .container {
    width: 1070px;
  }
}
body {
  color: #5f5f5f;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.5;
  font-family: "Montserrat", sans-serif;
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Playfair Display", serif;
}

a {
  text-decoration: none;
  transition: .3s ease;
  color: #ecad25;
}
a:focus, a:hover {
  text-decoration: none;
  color: #cacaca;
}

address {
  font-style: normal;
}

p {
  margin-top: 0;
}

iframe {
  display: block;
  border: none;
}

p,
ul,
ol,
pre,
h1,
h2,
h3,
h4,
h5,
h6,
table,
form {
  margin-bottom: 20px;
  line-height: 1.786em;
}

h1,
h2,
h3 {
  font-weight: normal;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 14px;
}

ins {
  text-decoration: none;
}

pre {
  background: none;
}

input.form-control {
  background: none !important;
  height: 20px;
}

input.form-control,
select.form-control {
  border-radius: 0px;
  border: 2px solid #222222;
  max-width: 100%;
}

embed,
iframe,
object {
  max-width: 100%;
}

/* blockquote */
blockquote {
  position: relative;
  padding: 0px;
  border: none;
  font-style: normal;
  line-height: 30px;
  color: #969595;
  margin: 10px 0px;
}
blockquote p {
  font-size: 22px;
  font-weight: 300;
  line-height: 30px;
}
blockquote cite {
  display: block;
  color: #999999;
  font-size: 0.6em;
  font-style: italic;
}

/* Image & Alignments */
img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

img[class*="wp-image-"] {
  margin-top: 10px;
  margin-bottom: 10px;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  max-width: 100%;
  margin-bottom: 20px;
  padding: 0 5px;
  border-radius: 4px;
}
.wp-caption.aligncenter {
  margin-bottom: 20px;
}
.wp-caption img {
  display: block;
  margin-top: 5px;
}
.wp-caption .wp-caption-text {
  margin: 10px 0;
  font-size: 13px;
  text-align: center;
}
.wp-caption .wp-caption-text img {
  display: inline;
  margin-top: 0;
  border-radius: 0;
}

.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em;
}

.aligncenter {
  display: block;
  clear: both;
  margin: 0 auto;
}

/* Gallery */
.gallery {
  margin-bottom: 20px;
}
.gallery .thumbnail {
  margin-bottom: 10px;
}
.gallery .thumbnail img {
  width: 100%;
  border-radius: 3px;
}

.gallery .caption,
.gallery-caption {
  margin-bottom: 20px;
  font-size: 12px;
  text-align: center;
}

@media (min-width: 768px) {
  .gallery-col-5 .col-sm-2 {
    width: 20%;
  }

  .gallery-col-7 .col-sm-1 {
    width: 14.28571428571429%;
  }

  .gallery-col-8 .col-sm-1 {
    width: 12.5%;
  }

  .gallery-col-9 .col-sm-1 {
    width: 11.11111111111111%;
  }
}
/* Clearing */
.clear {
  clear: both;
}

/* text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.screen-reader-text:hover, .screen-reader-text:active, .screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Bootstrap button style */
.btn {
  margin-bottom: 5px;
  padding: 0 20px;
  text-transform: uppercase;
  text-align: center;
  font-size: 12px;
  height: 36px;
  line-height: 32px;
  border-radius: 0;
  background: none;
  border: 2px solid #222222;
  color: #222222;
}
.btn:hover {
  background: none;
  border-color: #cacaca;
  color: #cacaca;
}
.btn.btn-primary {
  margin-bottom: 5px;
  padding: 0 20px;
  text-transform: uppercase;
  text-align: center;
  font-size: 12px;
  height: 36px;
  line-height: 32px;
  border-radius: 0;
  background: none;
  border: 2px solid #ecad25;
  color: #222222;
}
.btn.btn-primary:hover {
  background: none;
  border-color: #ecad25;
  color: #ecad25;
}

/* search form box */
.search-form-box {
  position: relative;
}
.search-form-box .search-field {
  border: 2px solid #222222;
  height: 36px;
  border-radius: 0;
  padding: 0 10px;
  font-size: 12px;
  width: 100%;
  box-sizing: border-box;
}
.search-form-box .search-submit {
  margin-bottom: 5px;
  padding: 0 20px;
  text-transform: uppercase;
  text-align: center;
  font-size: 12px;
  height: 36px;
  line-height: 32px;
  border-radius: 0;
  background: none;
  border: 2px solid #222222;
  color: #222222;
  box-sizing: border-box;
  background: #222222;
  color: #fff;
  padding: 0 10px;
  position: absolute;
  top: 0;
  right: 0;
}
.search-form-box .search-submit:hover {
  background: none;
  border-color: #cacaca;
  color: #cacaca;
}
.search-form-box .search-submit:hover {
  background: #cacaca;
  color: #222222;
  border-color: #222222;
}

/* social icons color */
.social-connect [class*="facebook"] {
  color: #3B5998;
}
.social-connect [class*="twitter"] {
  color: #55ACEE;
}
.social-connect [class*="google"] {
  color: #E02F2F;
}
.social-connect [class*="instagram"] {
  color: #974518;
}
.social-connect [class*="rss"] {
  color: #f26522;
}
.social-connect [class*="envelope"] {
  color: #999999;
}
.social-connect [class*="youtube"] {
  color: #e02b28;
}
.social-connect [class*="flickr"] {
  color: #0063db;
}
.social-connect [class*="linkedin"] {
  color: #007BB5;
}
.social-connect [class*="pinterest"] {
  color: #cc2127;
}
.social-connect [class*="dribbble"] {
  color: #E94C89;
}
.social-connect [class*="github"] {
  color: #000000;
}
.social-connect [class*="lastfm"] {
  color: #DA001C;
}
.social-connect [class*="vimeo"] {
  color: #1ab7ea;
}
.social-connect [class*="tumblr"] {
  color: #44546A;
}
.social-connect [class*="soundcloud"] {
  color: #FF4700;
}
.social-connect [class*="behance"] {
  color: #111111;
}
.social-connect [class*="deviantart"] {
  color: #536551;
}

/* 2.0 HEADER
-------------------------------------------------------------- */
/* top navigation */
.header-topnav {
  background: #222222;
  z-index: 999;
  font-size: 12px;
  height: 40px;
}
.header-topnav .welcome-message {
  color: #ffffff;
  font-family: "Playfair Display", serif;
  line-height: 40px;
  transition: line-height 1s;
}
.header-topnav .navbar-right {
  float: right !important;
  margin: 0 -15px 0 0;
}
.header-topnav .navbar-nav .dropdown .dropdown-toggle {
  height: 40px;
  color: #ffffff;
  font-family: "Playfair Display", serif;
  padding-top: 10px;
  padding-bottom: 10px;
  background: none !important;
}
.header-topnav .navbar-nav .dropdown .dropdown-toggle:hover, .header-topnav .navbar-nav .dropdown .dropdown-toggle:focus {
  background: none !important;
}
.header-topnav .navbar-nav .dropdown .dropdown-toggle i {
  color: #999999;
  font-size: 14px;
  line-height: inherit;
  vertical-align: middle;
}
.header-topnav .navbar-nav .dropdown .dropdown-toggle .topnav-label {
  padding-left: 10px;
}
.header-topnav .navbar-nav > li {
  float: left;
}
.header-topnav .navbar-nav > li > a {
  padding-top: 15px;
  padding-bottom: 15px;
}
.header-topnav .navbar-right .dropdown-menu {
  left: auto;
  right: 0;
}
.header-topnav .navbar-nav .dropdown-menu {
  width: 200px !important;
}
.header-topnav .navbar-nav .dropdown-menu.topnav-search-dropdown {
  width: 240px !important;
}
.header-topnav .navbar-nav .dropdown-menu.topnav-minicart-dropdown {
  width: 260px !important;
}
.header-topnav .navbar-nav .dropdown-menu,
.header-topnav .navbar-nav .open .dropdown-menu {
  position: absolute;
  float: left;
  min-width: 160px;
  padding: 15px 0;
  margin: 0;
  background-color: #ffffff;
  border: 1px solid #cccccc;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-top: none;
  border-radius: 0 0 4px 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}
.header-topnav .navbar-nav .dropdown-menu > li > a,
.header-topnav .navbar-nav .open .dropdown-menu > li > a,
.header-topnav .navbar-nav .open .dropdown-menu .dropdown-header {
  padding: 5px 30px 5px 30px !important;
  line-height: 20px !important;
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.header-topnav .navbar-nav .dropdown-menu > li > a i {
  display: inline-block;
  width: 17px;
}
.header-topnav .topnav-search-dropdown .woocommerce-product-search, .header-topnav .topnav-search-dropdown .search-form-box {
  margin: 0 15px;
}
.header-topnav .topnav-minicart-dropdown .widget {
  font-family: "Montserrat", sans-serif;
  font-size: 11px;
  margin: 0;
  padding: 0;
}
.header-topnav .topnav-minicart-dropdown li img {
  margin-right: 15px !important;
}
.header-topnav .topnav-minicart-dropdown li a.remove {
  line-height: inherit !important;
}
.header-topnav .topnav-minicart-dropdown .total {
  border-top: 1px solid #ddd;
  margin-top: 10px;
  padding: 10px 15px;
  border-bottom: 1px solid #ddd;
}
.header-topnav .topnav-minicart-dropdown .buttons {
  margin: 0;
  padding: 0 15px;
}
.header-topnav .topnav-minicart-dropdown .buttons .button {
  display: block;
}

/* main header */
.site-navigation {
  border-color: #ffffff;
  background: #ffffff;
  width: 100%;
  z-index: 99;
  margin-bottom: 0px;
}
.site-navigation .navbar-header .navbar-icon {
  padding: 0;
  margin-right: 0;
  border: none;
  color: #222222;
  font-size: 24px;
  line-height: 34px;
}
.site-navigation .navbar-header .navbar-icon:hover,
.site-navigation .navbar-header .navbar-icon:focus {
  background: none !important;
}
.site-navigation .navbar-header a.navbar-brand {
  font-size: 30px;
  height: auto;
  padding: 0;
  margin: 0;
}
.site-navigation .navbar-header a.navbar-brand img {
  margin-top: 5px;
}
.site-navigation .navbar-header a.navbar-brand .site-with-text {
  padding: 10px 0px;
  color: #222222;
  font-family: "Playfair Display", serif;
  text-transform: uppercase;
}
.site-navigation .navbar-header a.navbar-brand .site-with-image img.site-logo {
  max-height: 50px;
}
.site-navigation .navbar-header a.navbar-brand .site-tagline {
  margin: 0 0 0 12px;
  font-size: 17px;
  line-height: 10px;
}
.site-navigation .header-block {
  border-bottom: 1px solid #cacaca;
  padding: 40px 0;
  transition: padding 1s;
  -webkit-transition: padding 1s;
  -moz-transition: padding 1s;
}
.site-navigation .navbar-collapse {
  border: none;
}
@media (max-width: 991px) {
  .site-navigation .navbar-collapse {
    padding: 20px 0 0;
    margin: 0;
  }
}
.site-navigation .nav {
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.site-navigation .nav .dropdown-menu {
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.site-navigation .nav a, .site-navigation .nav a:visited {
  color: #222222 !important;
  background: none !important;
}
.site-navigation .nav a:hover, .site-navigation .nav a:focus {
  color: #e6a61c !important;
}
@media (min-width: 992px) {
  .site-navigation .nav > li > a {
    padding-left: 30px;
    padding-right: 0;
  }
  .site-navigation .nav .dropdown-menu > li > a {
    padding-top: 7px;
    padding-bottom: 7px;
  }
}

/* fixing smartmenu submenu icon positioning */
.navbar-nav.sm-collapsible .caret, .navbar-nav.sm-collapsible ul .caret {
  top: 0;
  margin-top: 8px;
  line-height: inherit;
}

/* 3.0 CONTENT
-------------------------------------------------------------- */
.main-content {
  padding: 70px 0;
}

#content {
  padding: 0px;
}

.entry .entry-header {
  margin-bottom: 50px;
}
.entry .entry-header figure {
  background: #f5f5f5;
  text-align: center;
}
.entry .entry-title {
  color: #222222;
  font-size: 35px;
  letter-spacing: 1px;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 40px;
  text-align: center;
  margin-top: 0;
}
.entry .entry-title a, .entry .entry-title a:visited {
  color: #222222;
}
.entry .entry-content {
  padding: 0 0 50px;
}
@media (min-width: 992px) {
  .entry .entry-content {
    padding: 0 100px 50px;
  }
}
.entry .entry-content .read-more-link {
  display: block;
}
.entry .entry-content .pagination > li > a, .entry .entry-content .pagination > li a > span {
  color: #ecad25;
}
.entry .entry-content .pagination > li > a:hover, .entry .entry-content .pagination > li > a:focus, .entry .entry-content .pagination > li a > span:hover, .entry .entry-content .pagination > li a > span:focus {
  color: #222222;
}
.entry.sticky {
  background: #f5f5f5;
  margin-bottom: 70px;
}
.entry.sticky .entry-header {
  margin-bottom: 0;
}
.entry.sticky .entry-title {
  padding-top: 50px;
}
.entry.sticky .entry-content {
  padding: 0 50px 50px;
}
@media (min-width: 992px) {
  .entry.sticky .entry-content {
    padding: 0 100px 50px;
  }
}
.entry.post-not-found {
  text-align: center;
}

.page .entry .entry-title {
  margin-bottom: 50px;
}

/* entry meta */
.entry-meta {
  margin-bottom: 30px;
  text-align: center;
}
.entry-meta .entry-meta-item {
  margin: 0 8px;
}
.entry-meta .entry-meta-item a, .entry-meta .entry-meta-item a:visited {
  color: #969595;
  font-weight: normal;
  font-size: 13px;
  text-transform: uppercase;
}
.entry-meta .entry-meta-item i {
  margin-right: 5px;
  color: #f2784b;
}
.entry-meta .entry-meta-item .updated {
  display: none;
}
.entry-meta .entry-meta-item .updated.published {
  display: inline;
}

/* entry footer */
.entry-footer a, .entry-footer a:visited {
  color: #222222;
}
.entry-footer .entry-meta-item span {
  font-size: 12px;
  text-transform: uppercase;
}
.entry-footer .entry-meta-item span i {
  font-size: 18px;
  line-height: 30px;
  color: #f2784b;
  vertical-align: middle;
  margin-right: 10px;
}
.entry-footer .entry-meta-item a {
  font-size: 12px;
  text-transform: uppercase;
}
.entry-footer .entry-meta-item div span {
  margin-right: 40px;
}
.entry-footer .entry-meta-item div span:after {
  content: '';
  width: 25px;
  height: 2px;
  background: #dcdcdc;
  display: inline-block;
  position: relative;
  top: -3px;
  right: -20px;
}

/* pagination */
.paging-navigation {
  text-align: center;
  width: 100%;
  margin: 0;
}
.paging-navigation ul.pagination {
  display: inline-block;
  margin: 0px;
  padding: 10px;
}
.paging-navigation ul.pagination li {
  display: inline-block;
  list-style: none;
  margin-right: 10px;
}
.paging-navigation ul.pagination li:last-child {
  margin-right: 0px;
}
.paging-navigation ul.pagination li span {
  border-radius: 2px;
}
.paging-navigation ul.pagination li .page-numbers {
  font-size: 12px;
  padding: 6px 10px;
  color: #1b1b1b;
  border: 2px solid #222222;
  text-transform: uppercase;
  border-radius: 2px;
}
.paging-navigation ul.pagination li .page-numbers:hover {
  background: #222222;
  color: white;
}
.paging-navigation ul.pagination li .page-numbers.dots {
  border: none;
}
.paging-navigation ul.pagination li .page-numbers.dots:hover {
  background: none;
  color: #1b1b1b;
}
.paging-navigation ul.pagination li.active .page-numbers {
  background: #222222;
  color: white;
}

/* post navigation */
.navigation.post-navigation {
  padding: 60px 0 0;
  margin: 40px 0 0;
  border-top: 1px solid #cacaca;
  position: relative;
  overflow: hidden;
}
.navigation.post-navigation .nav-previous,
.navigation.post-navigation .nav-next {
  font-size: 18px;
  text-transform: inherit;
}
.navigation.post-navigation .nav-previous a,
.navigation.post-navigation .nav-next a {
  color: #222222;
}
.navigation.post-navigation .nav-previous span,
.navigation.post-navigation .nav-next span {
  display: block;
}
.navigation.post-navigation .nav-previous span.post-title,
.navigation.post-navigation .nav-next span.post-title {
  font-family: "Playfair Display", serif;
}
.navigation.post-navigation .nav-previous span.post-date,
.navigation.post-navigation .nav-next span.post-date {
  color: #b8b8b8;
  font-size: 13px;
}
.navigation.post-navigation .nav-previous .desc-control,
.navigation.post-navigation .nav-next .desc-control {
  padding: 0;
  margin: 0 0 20px;
}
.navigation.post-navigation .nav-previous .desc-control i,
.navigation.post-navigation .nav-next .desc-control i {
  display: none;
  color: #f2784b;
  font-size: 18px;
  position: absolute;
  top: 6px;
  line-height: inherit;
}
@media (min-width: 768px) {
  .navigation.post-navigation .nav-next {
    font-size: 24px;
  }
  .navigation.post-navigation .nav-next .desc-control {
    padding: 0 50px;
    border-left: 1px solid #e8e8e8;
  }
  .navigation.post-navigation .nav-next .desc-control i {
    display: block;
    right: 30px;
  }
  .navigation.post-navigation .nav-previous {
    font-size: 24px;
  }
  .navigation.post-navigation .nav-previous .desc-control {
    padding: 0 50px;
  }
  .navigation.post-navigation .nav-previous .desc-control i {
    display: block;
    left: 30px;
  }
}
@media (min-width: 992px) {
  .navigation.post-navigation .nav-next .desc-control {
    padding: 0 100px;
  }
  .navigation.post-navigation .nav-next .desc-control i {
    right: 80px;
  }
  .navigation.post-navigation .nav-previous .desc-control {
    padding: 0 100px;
  }
  .navigation.post-navigation .nav-previous .desc-control i {
    left: 80px;
  }
}

/* 4.0 POST FORMAT
-------------------------------------------------------------- */
.entry-media {
  background: #f5f5f5;
  border-radius: 4px;
  margin-bottom: 50px;
}

.entry-media-wrap {
  position: relative;
  text-align: center;
}

.entry-media-wrap > *:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
}

.entry-media-wrap .wp-caption {
  display: inline-block;
}

/* format image */
.entry-media-image img {
  margin: 0;
  padding: 0;
}
.entry-media-image .thumbnail,
.entry-media-image .wp-caption {
  padding: 0;
  border: 0;
  border-radius: 0;
}
.entry-media-image .wp-caption .wp-caption-text {
  margin: 0;
  padding: 10px 10px;
}

/* format gallery */
.entry-media-gallery {
  padding: 2px;
}
.entry-media-gallery .gallery {
  padding: 30px 30px 0;
}
.entry-media-gallery .gallery .thumbnail {
  margin-bottom: 30px;
  padding: 0;
  border: none;
  background: none;
}
.entry-media-gallery .gallery .caption {
  display: none;
}

/* format audio */
.entry-media-audio {
  padding: 60px;
}
.entry-media-audio iframe {
  width: 100%;
}

/* format video */
.video-container {
  overflow: hidden;
  position: relative;
  height: 0;
  padding-top: 30px;
  padding-bottom: 56.25%;
}
.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-player {
  width: 100% !important;
}

